* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  padding-left: 0;
  list-style-type: decimal;
}

.country-info--flag-title {
  display: flex;
  align-items: center;
}

.flag-title--flag-img {
  margin-right: 20px;
}

.country-info--item > span {
  font-weight: 700;
}

.countries-list--item {
  list-style: none;
}

.country-info--list {
  padding-left: 0;
}

.country-info--item {
  list-style: none;
}
